import { Button, TextField, Typography } from '@mui/material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';
import { AppContext } from 'MainApp';
import React, { useContext, useState } from 'react';
import fr from 'date-fns/locale/fr';
import { format } from 'date-fns/esm';
import { Lock } from '@mui/icons-material';

export const VerifyID = () => {
  const { name, setName, openNewNotif, setVerified, ready, setReady } = useContext(AppContext);

  const [step, setStep] = useState(0);
  const [success, setSuccess] = useState(false);
  const [message, setMessage] = useState(false);

  const [value, setValue] = useState('');

  const changeStep = newStep => {
    if (newStep === 1) setValue(new Date());
    else setValue('');
    setStep(newStep);
    setSuccess(false);
    setMessage(null);
  };

  const solutions = [[`l'cho`, `cho`, `echo`], `08/05/2021 00:22`];

  const accept = type => e => {
    if (type) setReady(true);
    else setName(null);
  };

  const checkSoluce = e => {
    e.preventDefault();
    if (step === 0) {
      if (solutions[step].includes(value.toLowerCase())) {
        setSuccess(true);
        setMessage(`C'est bien a ! ( ${solutions[step][0]} )`);
        openNewNotif({ severity: 'success', message: 'Bonne rponse !' });
      } else {
        setSuccess(false);
        setMessage(`"${value}" n'est pas la bonne rponse !`);
        openNewNotif({ severity: 'error', message: 'Mauvaise rponse !' });
      }
    }
    if (step === 1) {
      const strDate = format(value, 'dd/MM/yyyy HH:mm');
      if (strDate === solutions[step]) {
        setSuccess(true);
        setMessage(`C'est bien a ! (${solutions[step]})`);
        openNewNotif({ severity: 'success', message: 'Bonne rponse !' });
      } else {
        setSuccess(false);
        setMessage(`Le "${strDate}" n'est pas la bonne rponse !`);
        openNewNotif({ severity: 'error', message: 'Mauvaise rponse !' });
      }
    }
  };

  return (
    <div className="verify">
      {!ready && (
        <div className="center-wrapper">
          <Typography>Avant de continuer, nous aimerions vrifier votre identit.</Typography>
          <br />
          <Typography>tes-vous prt.e ?</Typography>
          <br />
          <Button
            variant="contained"
            className="verify-button"
            color="success"
            onClick={accept(true)}
          >
            Oui
          </Button>
          <Button
            variant="contained"
            className="verify-button"
            color="error"
            onClick={accept(false)}
          >
            Non
          </Button>
        </div>
      )}
      {ready && step === 0 && (
        <div className="center-wrapper">
          <br />
          <Typography variant="h5">
            {success ? `nigme` : `Veuillez rpondre  l'nigme suivante :`}
          </Typography>
          <br />
          <Typography>Il parle toutes les langues sans en comprendre aucune.</Typography>
          <Typography>Il loge dans les hauteurs mais jamais dans les dunes.</Typography>
          <Typography>Il faut bien se garder de lui faire confiance.</Typography>
          <Typography>Car il va rpter la moindre confidence.</Typography>

          <br />
          {!success && (
            <>
              <form onSubmit={checkSoluce} className="whoareyou">
                <Typography className="consigne">Qu'est-ce ?</Typography>
                <TextField
                  label="Rponse"
                  variant="outlined"
                  value={value}
                  onChange={({ target: { value: v } }) => {
                    setValue(v);
                    setMessage(null);
                  }}
                />
                <br />
                <Button type="submit" variant="contained" className="whoareyou-button">
                  Valider
                </Button>
              </form>
            </>
          )}
          <br />
          {success && message && (
            <Typography variant="h6" color={'green'}>
              {`C'est bien a ! ( ${solutions[0][0]} )`}
            </Typography>
          )}
          {!success && message && (
            <Typography variant="h6" color={'red'}>
              {`"${value}" n'est pas la bonne rponse !`}
            </Typography>
          )}
          <br />
          {success && (
            <Button variant="contained" onClick={() => changeStep(1)}>
              Continuer
            </Button>
          )}
        </div>
      )}
      {ready && step === 1 && (
        <div className="center-wrapper">
          <br />
          <Typography variant="h5">Veuillez rpondre  la question suivante : </Typography>
          <br />
          <Typography>
            Il est preque sr que vous connaissiez dj la rponse  l'nigme prcdente.
          </Typography>
          <Typography>
            D'ailleurs, il semblerait qu'une personne aie trouver la rponse avant vous.
          </Typography>

          <br />
          {!success && (
            <>
              <form onSubmit={checkSoluce} className="whoareyou">
                <Typography className="consigne">
                   quelle date et  quelle heure, avez-vous envoyer votre premier <b>SMS</b> 
                  cette personne ?
                </Typography>
                <LocalizationProvider dateAdapter={AdapterDateFns} locale={fr}>
                  <DateTimePicker
                    label="Date et heure"
                    value={value}
                    onChange={v => {
                      setValue(v);
                      setMessage(null);
                    }}
                    renderInput={params => <TextField {...params} />}
                  />
                </LocalizationProvider>
                <br />
                <Button type="submit" variant="contained" className="whoareyou-button">
                  Valider
                </Button>
              </form>
            </>
          )}
          <br />
          {message && (
            <Typography variant="h6" color={success ? 'green' : 'red'}>
              {message}
            </Typography>
          )}
          <br />
          {success && (
            <Button variant="contained" onClick={() => changeStep(2)}>
              Continuer
            </Button>
          )}
        </div>
      )}
      {ready && step === 2 && (
        <div className="center-wrapper">
          <br />
          <Typography variant="h5">{`Impressionnant ${name} !`}</Typography>
          <br />
          <Typography>Votre identit est dsormais confirme.</Typography>

          <br />
          <Button variant="contained" onClick={() => setVerified(true)} startIcon={<Lock />}>
            Continuer de manire scuris
          </Button>
        </div>
      )}
    </div>
  );
};
